import React, { ReactElement, ReactNode } from 'react';
import { ModalFooter as ModalFooterWrap, ModalFooterAction } from './styled';

type IModalFooter = {
  action?: ReactNode;
  confirm?: ReactNode;
  cancel?: ReactNode;
  style?: React.CSSProperties;
};

export const ModalFooter = React.memo(
  (props: IModalFooter) => {

    const { action, confirm, cancel, style } = props;

    return <ModalFooterWrap style={style}>
      {action ? <ModalFooterAction>{action}</ModalFooterAction> : null}
      {cancel}
      {confirm}
    </ModalFooterWrap>;
  }
);
